:root {
    --app-background-dark: rgb(24, 23, 32);

}


main {
    height: 100svh;
    display: grid;
    place-items: center;
}

body {
    background-color: var( -- --app-background-dark);
}

.container {
    width: min(100% - 30px, 1200px);
    margin-inline: auto;
}



section {
    margin-top: 60px;
}

p {
    font-size: 0.825rem;
}

h1, h2, h3, p, li, a {
    color: #fff;
}

div {
    text-align: center;
}

h2 {
    font-size: 4rem;
    font-weight: 400;
    line-height: 1;
}

span {
    color: #00f3b6;
}

input {
    width: 280px;
    height: 44px;
    margin-top: 40px;
    padding: 0 15px;
    border-radius: 50px;
    border: 1px solid rgba(255, 255, 255, 0.05); 
    background-color: rgba(255, 255, 255, 0.05);
    font-size: 1.8rem;
    text-align: center;
    color:#fff;
}


